import {
  Avatar,
  Divider,
  IconButton,
  ListItemText,
  Typography,
} from "@mui/material";
import ListItem from "@mui/material/ListItem";
import ListItemAvatar from "@mui/material/ListItemAvatar";
import FavoriteIcon from "@mui/icons-material/Favorite";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import { Box } from "@mui/system";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { likeCharacter } from "../store/actions";

const colors = {
  Alive: "ForestGreen",
  Dead: "tomato",
  unknown: "LightGray",
};

// eslint-disable-next-line react/prop-types
const Character = ({ name, status, image, id, last }) => {
  let navigate = useNavigate();
  const dispatch = useDispatch();

  const { favorites } = useSelector((store) => store);
  const isFavorite = favorites.includes(id);

  const handleClick = () => {
    navigate(`${id}`);
  };

  const handleLike = (e) => {
    e.stopPropagation();
    dispatch(likeCharacter(id));
  };

  return (
    <>
      <ListItem
        onClick={handleClick}
        secondaryAction={
          <IconButton edge="end" aria-label="favorite" onClick={handleLike}>
            {isFavorite && <FavoriteIcon sx={{ color: "pink" }} />}
            {!isFavorite && <FavoriteBorderIcon />}
          </IconButton>
        }
      >
        <ListItemAvatar>
          <Avatar alt={name} src={image} />
        </ListItemAvatar>
        <ListItemText primary={name} sx={{ cursor: "default" }} />
        <Box
          sx={{
            minWidth: 110,
            backgroundColor: colors[status],
            height: 40,
            borderRadius: 10,
            display: "flex",
          }}
        >
          <Typography sx={{ margin: "auto", textTransform: "uppercase" }}>
            {status}
          </Typography>
        </Box>
      </ListItem>
      {!last && <Divider />}
    </>
  );
};

export default Character;
