import thunk from "redux-thunk";
import { createStore, applyMiddleware } from "redux";
import { composeWithDevTools } from "redux-devtools-extension";
import {
  LOADING_CHARACTERS,
  GET_CHARACTERS,
  GET_CHARACTER,
  LIKE_CHARACTER,
  SET_SELECTED_ITEM,
  SET_PAGE,
  SET_NAME,
  SET_AUTH,
} from "./types";

const initialState = {
  name: "",
  suggestions: [],
  characters: [],
  pages: null,
  currentPage: 1,
  favorites: [],
  isLoading: false,
  currentCharacter: null,
  currentCharactersEpisodes: [],
  selectedItem: null,
  isAuth: false,
};

const logger = (store) => (next) => (action) => {
  console.group(action.type);
  console.info("dispatching", action);
  let result = next(action);
  console.log("next state", store.getState());
  console.groupEnd(action.type);
  return result;
};

// let createStoreWithMiddleware = applyMiddleware(logger)(createStore)

// let yourApp = combineReducers(reducers)
// let store = createStoreWithMiddleware(yourApp)

const rootReducer = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case SET_AUTH:
      return {
        ...state,
        isAuth: payload,
      };
    case LOADING_CHARACTERS:
      return { ...state, isLoading: payload };
    case GET_CHARACTERS:
      return {
        ...state,
        characters: payload.characters,
        pages: payload.pages,
        suggestions: payload.suggestions,
      };
    case GET_CHARACTER:
      return {
        ...state,
        currentCharacter: payload.data,
        currentCharactersEpisodes: payload.episodes,
      };
    case LIKE_CHARACTER: {
      const newFavorites = state.favorites.includes(payload)
        ? state.favorites.filter((item) => item !== payload)
        : [...state.favorites, payload];
      return {
        ...state,
        favorites: newFavorites,
      };
    }
    case SET_SELECTED_ITEM:
      return {
        ...state,
        selectedItem: payload,
      };
    case SET_PAGE:
      return {
        ...state,
        currentPage: payload,
      };
    case SET_NAME:
      return {
        ...state,
        name: payload,
      };
    default:
      return { ...state };
  }
};

const store = createStore(
  rootReducer,
  initialState,
  composeWithDevTools(applyMiddleware(thunk, logger))
);

export default store;
